@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"), url("../fonts/Roboto-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.ttf") format("truetype");
  font-weight: 700;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
}
@supports (font-variation-settings: normal) {
  @font-face {
    font-family: "Open Sans";
    src: url("../fonts/OpenSans-VariableFont_wdth,wght.woff2") format(woff2) tech(variations), url("../fonts/OpenSans-VariableFont_wdth,wght.woff2") format("woff2-variations");
    font-weight: 100 900;
  }
}
/** Media Queries **/
html {
  font-size: 16px;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1rem;
  background-color: #f0f0f0;
  margin: 0;
}

.wrapper {
  inline-size: 95%;
  max-inline-size: 75rem;
  margin: 0 auto;
}

img {
  max-inline-size: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 2.75rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 2.5rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 1.875rem;
}

[class$=title] {
  margin: 1.25rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1rem;
  line-height: 1.8;
}

.header {
  background-size: cover;
  background-position: center;
  padding: 3rem 0;
  position: relative;
}
@supports (background-image: url(../img/header_bg.jpg)) {
  .header {
    background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
  }
}
@supports (background-image: url(../img/header_bg.webp)) {
  .header {
    background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.webp);
  }
}
@supports (background-image: url(../img/header_bg.avif)) {
  .header {
    background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.avif);
  }
}
@media (min-width: 768px) {
  .header {
    padding: 1.875rem 0 12.5rem 0;
  }
}
.header__bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.header__logo {
  inline-size: 15.625rem;
}
.header__title {
  color: #FFFFFF;
  text-align: center;
}
.header__text {
  color: #FFFFFF;
  display: none;
}
.header__img {
  display: none;
}
@media (min-width: 768px) {
  .header__bar {
    flex-direction: row;
    justify-content: space-between;
    gap: 1.5rem;
  }
  .header__content {
    margin-block-start: 6.25rem;
    max-inline-size: 31.25rem;
  }
  .header__title {
    text-align: left;
  }
  .header__text {
    display: block;
  }
  .header__img {
    display: block;
    position: absolute;
    inset-inline-end: 0;
    inset-block-end: -3.125rem;
    max-inline-size: 21.875rem;
    border-top-left-radius: 1.25rem;
    border-bottom-left-radius: 3.125rem;
  }
}
@media (min-width: 1200px) {
  .header__img {
    max-inline-size: 37.5rem;
  }
}

.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-block-start: 3.125rem;
}
.navigation__link {
  color: #FFFFFF;
  font-size: 1.125rem;
  margin-block-end: 1.25rem;
}
.navigation__link:last-of-type {
  margin-block-end: 0;
}
@media (min-width: 768px) {
  .navigation {
    flex-direction: row;
    gap: 1.25rem;
    margin-block-start: 0;
  }
  .navigation__link {
    margin-block-end: 0;
  }
}

.properties {
  margin-block-start: 3.125rem;
}
@media (min-width: 768px) {
  .properties {
    margin-block-start: 6.25rem;
  }
}
.properties__title {
  font-weight: 400;
}
.properties__title span {
  font-weight: 700;
}
.properties__title::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #6277a1 100%);
}
.properties__content {
  display: grid;
  gap: 1.25rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .properties__content {
    display: grid;
    gap: 1.25rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.property {
  background-color: #FFFFFF;
}
.property__img {
  aspect-ratio: 10/6;
  background-size: auto 150%;
  position: relative;
}
@supports (background-image: url("../img/propiedad_1.jpg")) {
