.property__name {
  position: absolute;
  inset-inline-start: 0.625rem;
  inset-block-end: 0.625rem;
  color: #FFFFFF;
  font-size: 1.5rem;
}
.property__detail {
  padding: 1.25rem;
}
.property__text {
  margin-block-end: 0;
}
.property__price {
  font-size: 1.875rem;
  margin: 0;
}
.property__link {
  background-color: #3b4862;
  padding: 1rem 2.5rem;
  color: #FFFFFF;
  margin-block-start: 1.25rem;
  font-size: 1.25rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .property__link {
    inline-size: auto;
  }
}
.property__link:hover {
  background-color: #283142;
}